* {
  box-sizing: border-box;
  list-style: none;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  background-image: url('https://static.wixstatic.com/media/261ef1_2b950a1f4c324d37aae7374098e7cdb7~mv2.gif');
  background-repeat: no-repeat;
  background-size: cover;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
  display: block;
  margin: 150px auto 0;
}

.card-title {
  text-align: center;
  font-size: 50px;
}

.card-text {
  font-size: 20px;
}

.country-name {
  font-size: 20px;
}

.country-list {
  text-align: center;
  margin: 50px auto 0;
  font-size: 20px;
}
